<template>
    <label
        class="checkbox-block"
        :class="className"
    >
        <input
            class="checkbox-block__hidden"
            type="checkbox"
            :checked="checkboxState"
            v-model="checkboxState"
            @change="isChanged"
        >
        <div class="checkbox-block__icon">
            <img
                :src="CheckboxIcon"
                alt="checked icon"
            >
        </div>
        <div class="checkbox-block__text">
            {{ text }} <slot></slot>
        </div>
        
    </label>
</template>

<script>
import CheckboxIcon from '@/assets/img/checkbox.svg'

export default {
    props: {
        className: {
            type: String,
            default: ''
        },
        text: {
            type: String,
            required: true
        }
    },
    data () {
        return {
            CheckboxIcon,
            checkboxState: false
        }
    },
    methods: {
        isChanged: function() {
            this.$emit('on-change', this.checkboxState)
        }
    }
}
</script>

<style lang="scss" scoped>
.checkbox-block {
    @extend %f-cy;
    cursor: pointer;

    &__hidden {
        display: none;

        &:checked + .checkbox-block__icon {
            border: 1px solid var(--color-cerulean);

            & > img {
                opacity: 1;
            }
        }
    }

    &__icon {
        border: 1px solid var(--color-gallery);
        width: 18px;
        height: 18px;
        border-radius: 4px;
        transition: border-color var(--tr-xshort) ease;
        margin-right: 15px;

        & img {
            opacity: 0;
            transition: opacity var(--tr-xshort) ease;
            width: 18px;
            height: 18px;
        }
    }
}
</style>