<template>
    <div class="settings-wrapper">
        <SettingBlock
            title="Налаштування інтерфейсу"
        >
            <InputBlock
                id="221"
                label="Мова сайту"
                errorMessage="Мова сайту"
                value="Українська"
            />
        </SettingBlock>

        <SettingBlock
            title="Змінити пароль"
        >
            <InputBlock
                v-for="item in passwordsInput"
                :key="item.id"
                :id="item.id"
                :type="item.type"
                :label="item.label"
                :showError="item.showError"
                :errorMessage="item.errorMessage"
                @handle-input="handleInput($event, item.id, item.name)"
            >
                <button
                    class="show-password-btn"
                    @click="showPassword(item.id)"
                    :class="{'show-password-btn_active': item.type === 'text'}"
                >
                    <EyeIcon />
                </button>
            </InputBlock>
        </SettingBlock>

        <SettingBlock
            title="Пов'язані аккаунти"
        >
            <div
                class="linked-account"
                v-for="item in linkedAccount"
                :key="item.id"
            >
                <div class="linked-account__icon">
                    <img :src="item.icon" alt="icon">
                </div>
                <div class="linked-account__text">
                    {{ item.text }}
                </div>
                <button class="linked-account__delete">Відв’язати</button>
            </div>
        </SettingBlock>

        <SettingBlock
            title="Email розсилки"
        >
            <Checkbox
                v-for="item in mailing"
                :key="item.id"
                :text="item.text"
                className="mailing-checkbox"
                @on-change="checkboxChangeHandler($event, item.id)"
            >
                <img :src="HelpIcon" alt="help icon">
            </Checkbox>
        </SettingBlock>
        <Button
            :text="saveButton"
            className="save-button"
        />
    </div>
</template>

<script>
import SettingBlock from '@/components/SettingsPage/SettingBlock'
import InputBlock from '@/components/Input'
import Checkbox from '@/components/Checkbox'
import Button from '@/components/Buttons/Button'

import EyeIcon from '@/components/SvgComponents/EyeIcon'
import GoogleIcon from '@/assets/img/icon-google.svg'
import HelpIcon from '@/assets/img/icon-help-be-volunteer.svg'

export default {
    components: {
        SettingBlock, InputBlock, Checkbox, Button, EyeIcon
    },
    data () {
        return {
            HelpIcon,
            equalPasswords: true,
            passwordsInput: [
                {
                    id: 1,
                    type: 'password',
                    label: 'Старий пароль',
                    value: '',
                    name: 'old',
                    showError: false,
                    errorMessage: 'Невірний пароль'
                },
                {
                    id: 2,
                    type: 'password',
                    label: 'Новий пароль',
                    value: '',
                    name: 'new',
                    showError: false,
                    errorMessage: 'Паролі не співпадають'
                },
                {
                    id: 3,
                    type: 'password',
                    label: 'Новий пароль ще раз',
                    value: '',
                    name: 'new',
                    showError: false,
                    errorMessage: 'Паролі не співпадають'
                }
            ],
            linkedAccount: [
                {
                    id: 11,
                    icon: GoogleIcon,
                    text: 'Google'
                }
            ],
            mailing: [
                {
                    id: 111,
                    text: 'Хочу отримувати листи від Theatre.love на e-mail',
                    state: false
                },
                {
                    id: 222,
                    text: 'Хочу отримувати підбірки вистав, засновані на моїх вподобаннях',
                    state: false
                },
            ],
            saveButton: 'Зберегти'
        }
    },
    methods: {
        checkboxChangeHandler: function(state, id) {
            this.mailing.forEach(item => item.id === id ? item.state = state : null)
        },
        handleInput: function(value, id, name) {
            this.passwordsInput.forEach(item => item.id === id ? item.value = value: null)

            let equal = false
            this.passwordsInput.forEach(item => {
                item.id !== id && item.name === 'new'
                ? equal = item.value === value
                : null
            })

            if (name !== 'old') {
                this.passwordsInput.forEach(item => item.name === 'new' ? item.showError = !equal : null)
            }
        },
        showPassword: function(id) {
            this.passwordsInput.forEach(item => {
                if (item.id === id) {
                    item.type === 'password'
                    ? item.type = 'text'
                    : item.type = 'password'
                }
            })
        }
    }
}
</script>

