    border: none;
    background-color: transparent;
    width: 36px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
    z-index: 2;
    cursor: pointer;
    background-color: var(--color-shaft-dark);

    & svg {
        fill: var(--color-white);
        transition: fill var(--tr-xshort) ease;
    }

    &_active {

        & svg {
            fill: var(--color-cerulean);
        }
    }
}
.linked-account {
    border: 1px solid var(--color-mine-shaft);
    border-radius: var(--default-border-radius);
    padding: 10px 20px 10px 15px;
    @extend %f-cy;

    &__icon {
        @extend %f-cy;
        margin-right: 15px;
    }

    &__delete {
        margin-left: auto;
        border: none;
